const styles = theme => ({
    container: {
        margin: '10px 16%',
    },
    desc: {
        paddingTop: "0",
    },
    button: {
        margin: theme.spacing.unit,
    },
    leftIcon: {
        marginRight: theme.spacing.unit,
    },
    rightIcon: {
        marginLeft: theme.spacing.unit,
    },
    iconSmall: {
        fontSize: 20,
    },

    card: {
        display: 'flex',
        // width: "600px",
        height: "280px",
        margin: "auto"

    },
    details: {
        display: 'flex',
        flexDirection: 'column',
    },

    media: {
        // ⚠️ object-fit is not supported by IE 11.
        objectFit: 'cover',
        maxWidth: 200,
        align: 'center'

    },
    content: {
        // flex: '1 0 auto',
    },
    cover: {
        width: 200,
        right: 0
    },
    controls: {
        display: 'flex',
        alignItems: 'center',
        paddingLeft: theme.spacing.unit,
        paddingBottom: theme.spacing.unit,
    }
});

export default styles;